.app-header-container {
  width: 100%;
  text-align: center;

  .vendor-cta {
    color: $clr1;
    font-size: 1.5rem;

    span {
      font-family: FbOgen-black;
    }
  }

  .nav-container {
    background-color: $clr1;
    color: #fff;

    ul {
      height: 80px;
      display: flex;
      align-items: center;
      justify-content: space-around;

      li {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
      }

      img {
        width: 150px;

        @media screen and (min-width: 700px) {
          width: 200px;
        }
      }

      button {
        font-family: FbOgen-black;
        background-color: #fff;
        color: $clr1;
        border: 0;
        border-radius: 5rem;
        width: 90px;
        height: 30px;
        font-size: 1.3rem;

        @media screen and (min-width: 700px) {
          width: 150px;
          height: 40px;
          font-size: 1.7rem;
        }
      }
    }
  }
}
